import './adapter';

import { extensions, INSTALLED } from 'pixijs/core';
import { AlphaFilter } from 'pixijs/filter/alpha';
import { BlurFilter, BlurFilterPass } from 'pixijs/filter/blur';
import { ColorMatrixFilter } from 'pixijs/filter/color-matrix';
import { DisplacementFilter } from 'pixijs/filter/displacement';
import { FXAAFilter } from 'pixijs/filter/fxaa';
import { NoiseFilter } from 'pixijs/filter/noise';
import 'pixijs/display/cache-as-bitmap';
import 'pixijs/display/get-child-by-name';
import 'pixijs/display/get-global-position';
// eslint-disable-next-line @typescript-eslint/no-duplicate-imports
import { NodeCanvasResource } from './adapter';
import { loadTextures, loadWebFont } from 'pixijs/assets';
import { ResizePlugin } from 'pixijs/app';
import { loadBitmapFont } from 'pixijs/text-bitmap';

// Remove the default loader plugins
extensions.remove(
    loadTextures,
    loadWebFont,
    loadBitmapFont,
    ResizePlugin
);

// reset installed resources and remove resize plugin from Application
INSTALLED.length = 0;
INSTALLED.push(NodeCanvasResource);

export const filters = {
    AlphaFilter,
    BlurFilter,
    BlurFilterPass,
    ColorMatrixFilter,
    DisplacementFilter,
    FXAAFilter,
    NoiseFilter,
};

// Export ES for those importing specifically by name,
export * from 'pixijs/app';
export * from './adapter';
export * from 'pixijs/assets';
export * from 'pixijs/core';
export * from 'pixijs/display';
export * from 'pixijs/extract';
export * from 'pixijs/graphics';
export * from 'pixijs/mesh';
export * from 'pixijs/mesh/extras';
export * from 'pixijs/particle-container';
export * from 'pixijs/prepare';
export * from 'pixijs/sprite';
export * from 'pixijs/sprite-animated';
export * from 'pixijs/sprite-tiling';
export * from 'pixijs/spritesheet';
export * from 'pixijs/text';
export * from 'pixijs/text-bitmap';
